import Utils from '../../basic/Utils.js';
import dataInterface from '../../data/DataInterface.js';

/**
 * detects large map with only string keys,
 * they are better off switched to object, which is more memory efficient
 * 
 * {"objectId":2461796990144, "typeId":2461775916480,
      "addInfo":{"addType":"mapInfo", "mapInfo":[
      [{"ptrIdVal":2461796939072}, {"ptrIdVal":2461796939072}],
      [{"ptrIdVal":2461797033152}, {"ptrIdVal":2461797033152}],
      [{"ptrIdVal":2461797034048}, {"ptrIdVal":2461797034048}],
      [{"ptrIdVal":2461797035136}, {"ptrIdVal":2461797035136}],
      [{"ptrIdVal":2461797031424}, {"ptrIdVal":2461797031424}]]}
    },
 */
class DirectionaryChecker {
    constructor() {
        this.largeMapThreshold = 0;
    }
    isLargeMap(object) {
        if (!object.addInfo || !object.addInfo.mapInfo) return;
        return object.addInfo.mapInfo.length > 
            this.largeMapThreshold;
    }
    check(object, treeNode) {
        // guard checking
        if (!Utils.HOP(object, 'objectId')) return;
        if (!object.addInfo || !object.addInfo.mapInfo) return;
        if (!this.isLargeMap(object)) return;
        let mapInfo = object.addInfo.mapInfo;
        for (let pair of mapInfo) {
            if (!(pair[0].ptrIdVal)) return;
            let type = dataInterface.getJsTypeNameByObjectId(pair[0].ptrIdVal);
            if (type !== 'String') return;
        }
        // set the flag in the UI tree node
        Utils.addIssue(treeNode, {
            type: 'inefficient-map-object',
            name: `Map with only string keys
                   (Object is more memory-efficient)`,
        });
    }
}

export default new DirectionaryChecker();



