/**
 * manages resouces loading from the server side
 * 
 * Author: Liang Gong
 */
import axios from 'axios';
import RtConfig from '../basic/Config.js';
import sharedObj from '../basic/Global.js';
import Parser from './StreamJSON.js';

let oboe = require('oboe');

let JSONStream = require('JSONStream');
/*
let request = require('request'),
    JSONStream = require('JSONStream'),
    _ = require('highland');
*/

if (!sharedObj.resourceLoader) {
    let resourceLoader = {
        server: window.location.hostname,
        port: window.location.port,
        cache: {},
        dataInfo: [
            { name: 'prop', path: `data/prop.json` },
            { name: 'allocTrace', path: `data/allocTracing_0.json` },
            { name: 'snapshot', path: `data/snap_1.json` },
        ],
    };
    /**
     * initialize the resource loader;
     */
    resourceLoader.init = function() {
        for (let info of this.dataInfo) {
            if (this.cache[info.name]) continue;
            this.cache[info.name] = {};
        }
    };
    resourceLoader.load = function() {
        let url;
        for (let info of this.dataInfo) {
            url = `http://${this.server}:${this.port}/${info.path}`;
            this.testAndLoad(url, info.name);
        }
    };

    resourceLoader.testAndLoad = function(url, name) {
        this.getResourceSize(url, name, (file_len) => {
            if (file_len > 64 * 1024 * 1024) {
                // if file size is too big, load in chunks
                this.loadInChunks(url, name);
            } else {
                // other wise load in batch
                this.loadByAxios(url, name);
            }
        });
    };

    resourceLoader.getResourceSize = function(url, name, cb) {
        var xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function(){
            if(xhr.readyState == 4 && xhr.status == 206){
                let len = xhr.getResponseHeader("Content-Range").split("/")[1];
                cb(parseInt(len, 10));
            }
        };
        xhr.open("get", url, true);
        xhr.setRequestHeader("Range", "bytes=" + 0 + "-" + 1000);
        xhr.send();
    };

    resourceLoader.loadInChunks = function(url, name) {
        var chunk_size = 2 * 1024 * 1024; // 4MB chunks
        var p = new Parser();
        var _this = this;
        p.onValue = function (data) {
            if (this.stack.length === 0) {
                _this.cache[name] = data;
                sharedObj.emitAlways(`load-${name}`, data);
            }
        };
        function next_chunk(pos, file_len){
            if (pos > file_len) return;
            sharedObj.emit('show-status', `loading ${name} (${(100 * pos/file_len).toFixed(2)}%)...`);
            var chunk_end = pos + chunk_size < file_len ? pos + chunk_size : file_len;
            var xhr = new XMLHttpRequest();
            
            xhr.onreadystatechange = function(){
                if(xhr.readyState == 4 && xhr.status == 206){
                    if (!p) return;
                    p.write(xhr.responseText);
                    //request next chunk
                    file_len = parseInt(xhr.getResponseHeader("Content-Range").split("/")[1]);
                    setTimeout(next_chunk.bind(null, chunk_end + 1, file_len), 0);
                }
            };
            xhr.open("get", `${url}?v=${Math.random()}`, true);
            xhr.setRequestHeader("Range", "bytes=" + pos + "-" + chunk_end);
            xhr.send();
        }
        
        next_chunk(0, chunk_size);
    };

    resourceLoader.loadInChunks2 = function(url, name) {
        var chunk_size = 32 * 1024 * 1024; //1MB chunks
        var stream = JSONStream.parse('*');
        function next_chunk(pos, file_len){
            if(pos > file_len) return;
            sharedObj.emit('show-status', `loading chunk ${name} @ ${pos}...`);
            var chunk_end = pos + chunk_size < file_len ? pos + chunk_size : file_len;
            var xhr = new XMLHttpRequest();
            xhr.onreadystatechange = function(){
                if(xhr.readyState == 4 && xhr.status == 206){
                    //request next chunk
                    file_len = parseInt(xhr.getResponseHeader("Content-Range").split("/")[1]);
                    next_chunk(chunk_end + 1, file_len);
                }
            };
            xhr.open("get", url, true);
            xhr.setRequestHeader("Range", "bytes=" + pos + "-" + chunk_end);
            xhr.send();
        }
        stream.on('data', (data) => {
            this.cache[name] = data;
            sharedObj.emitAlways(`load-${name}`, data);
        });
        next_chunk(0, chunk_size);
    };

    resourceLoader.loadInChunks3 = function(url, name) {
        var stream = JSONStream.parse('*');
        fetch(url).then((res) => {
            /*const reader = res.body.getReader();
            reader.read().then(function process(result) {
                if (result.done) return;
                console.log(result);
                return reader.read().then(process);
            });
            */
            res.body.pipeThrough(stream);
        });
        stream.on('data', (data) => {
            this.cache[name] = data;
            sharedObj.emitAlways(`load-${name}`, data);
        });
    };

    resourceLoader.loadInChunks4 = function(url, name) {
        fetch(url).then((res) => {
            /*const reader = res.body.getReader();
            reader.read().then(function process(result) {
                if (result.done) return;
                console.log(result);
                return reader.read().then(process);
            });
            */
            oboe(res.body).node('*', (ctx) => {
                console.log(ctx);
            }).fail(() => {
                console.log('fail');
            }).done((data) => {
                this.cache[name] = data;
                sharedObj.emitAlways(`load-${name}`, data);
            });
        });
    };
    /**
     * loading the data through oboe
     */
    resourceLoader.loadByOboe = function(url, name) {
        let resCallback = data => {
            if (typeof data === 'string') {
                sharedObj.emit('show-status', `parsing ${name}...`);
                data = JSON.parse(data);
            }
            this.cache[name] = data;
            sharedObj.emitAlways(`load-${name}`, data);
        };

        sharedObj.emit('show-status', `loading ${name} from ${url}...`);
        oboe({url})
        .node('primitives.*', (data) => {
            console.log(data);
        })
        .fail(function(){
            console.log('fail');
        })
        .done(resCallback);
    };
    /**
     * loading the data through request and JSONStream
     */
    /*
    resourceLoader.load_stream = function() {
        let url, data;
        for (let info of this.dataInfo) {
            let resCallback = data => {
                if (typeof data === 'string') {
                    sharedObj.emit('show-status', `parsing ${info.name}...`);
                    data = JSON.parse(data);
                }
                this.cache[info.name] = data;
                sharedObj.emitAlways(`load-${info.name}`, data);
            };
            url = `http://localhost:3000/${info.path}`;
            sharedObj.emit(
                'show-status',
                `loading ${info.name} from ${url}...`
            );
            _(request({ url }).pipe(JSONStream.parse())).map(resCallback);
        }
    };
    */

    /**
     * loading the data through axios
     */
    resourceLoader.loadByAxios = function(url, name) {
        let data;
        sharedObj.emit('show-status', `loading ${name} from ${url}...`);
        axios.get(url).then(res => {
            if (typeof res.data === 'string') {
                sharedObj.emit('show-status', `parsing ${name}...`);
                data = JSON.parse(res.data);
                // release the JSON string
                res.data = '';
            } else {
                data = res.data;
            }
            this.cache[name] = data;
            sharedObj.emitAlways(`load-${name}`, data);
        });
    };

    resourceLoader.init();
    sharedObj.resourceLoader = resourceLoader;
}

export default sharedObj.resourceLoader;



